.blog-container{
    overflow: hidden;
    font-family: 'Fraktion Mono';
    font-size: 2vw;
    font-weight: 700;
    border: black solid 0.2vw;
    padding: 5vw;
    background: #ccff00;

    .pre-btn,
    .nxt-btn {
    font-family: "Supply";
    font-size: 1vw;
    font-weight: 700;
    border: none;
    width: 5vw;
    height: 100%;
    position: absolute;
    top:0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #d4f15c;
    cursor: pointer;
    opacity: 0.5;
    z-index: 8;
    &:hover{
        opacity: 1;
    }
    }

    .pre-btn{
        left: 0;
    }
    .nxt-btn{
        right: 0;
    }
    
    .blog-list{
        padding: 0 10vw;
        overflow-x: hidden;
        display: flex;
        scroll-behavior: smooth;
        max-width: 100vw;
        height: 44vw;
        text-decoration: none;
        .disable-scrollbars {
            scrollbar-width: none; /* Firefox */
            -ms-overflow-style: none;  /* IE 10+ */
          
            &::-webkit-scrollbar {
              background: transparent; /* Chrome/Safari/Webkit */
              width: 0px;
            }
        }
        .blog-item{
            margin-right: 2vw;
            border: black solid 0.2vw;
            display: flex;
            max-height: 70vh;
            font-family: 'Supply';
            color: black;
            background: white;
            .article{
                text-transform: uppercase;
                z-index: 20;
                width: 10vw;
                margin-top: 9vw;
                margin-left: 1vw;
                font-size: 1vw;
                /* Rotate from top left corner (not default) */
                transform-origin: 0 0;
                transform: rotate(-90deg);
            }
            .blog-img{
                border-right: solid black 0.2vw;
                height: 70vh;
                z-index: 1;
                background-size:cover;
                background-position: center;
                transform-origin: center;
                width: 100%;
                height: 100%;
                will-change: transform;
                transform: scale(1);
                transition: all 1.5s cubic-bezier(0.77, 0, 0.175, 1);
                filter: grayscale(100%)  brightness(80%); 
                
            }
            .text-info{ 
                border-left: black solid 0.2vw;
                margin-left: -3vw;
                background: white;
                z-index: 20;
                width: 55vw; 
                font-weight: 500;
                position: relative;
                z-index: 1;
                padding: 1vw;
                .title{
                    h1{
                    font-family: "Supply";
                    text-align:left;
                    position: relative;
                    font-weight: 700;
                    font-size: 2vw;
                    padding-top: 1vw ;
                    margin-bottom: 1vw ;
                    }
                    
                
                }
                p { 
                    position: relative;
                    font-family: 'Fraktion Mono';
                    font-weight: 500;
                    font-size: 1vw;
                    padding-bottom: 1vw;
                    
                }
                h5{
                    font-weight: 500;
                    font-size: 1vw;
                    line-height: 1vw;
                    margin: 1vw;
                    margin-left: 0;
                    position: relative;
                }
                a{
                    &:hover{
                        text-decoration: line-through;
                        
                    }    
                    text-decoration: none;
                    color: inherit;
                    font-size: 2vw;
                    line-height: 1vw;
                    margin-left: 0;
                    
                    position: relative;
                }
            }
        
          
        }
    }


      
 
}

.blog-title{
    font-family: "Supply";
    font-weight: 600;
    font-size: 1vw;
    margin-bottom: 5vw;
    text-transform: uppercase;
    color: black;
    text-align: left;
}


