.navbar {
    height: 5vh;
    display: flex;
    justify-content: space-between;
    padding: 1vw 1vw;
    font-size: 2vw;
    font-family: 'Supply';
    font-weight: 600;
    color: #ccff00;
    text-transform: uppercase;
    background: black;

    .menu{
        
        padding-right: 1vw;
        text-transform: capitalize;
        text-decoration: none;
    }

    a{ 
        color: inherit;
    } 
    #icons{
        
        #mail,#linkd,#git{
            color: #ccff00;
            text-shadow: 3px 3px 10px black;
            margin-left: 1vw;
        
            &:hover{
                color: whitesmoke;
                
            }    
        }
    }
    ul{ 
        
        opacity: 1;
        padding-left: 0vw;
        margin-top: 0;
        display:flex;
        font-size: 2vw;
        border: 0;
        list-style-type: none;

    
    }
}