.contact-page{
    display: flex;
    overflow: hidden;
    background-color: black ;
    color:  #ccff00;
    font-family: "Supply";
    font-weight: 700;
    font-size: 7vw;
    text-align: left;
    line-height: 1;
    border: solid black 0.2vw;
  
   
    #contact-content{
        
        text-shadow: 1px 1px 1px black;
        width:60vw;
        text-align: left;
        #header{
            text-shadow: 3px 3px  black;
            line-height: 0.9; 
            padding: 5vw;
            padding-top: 8vw;
            width: 50vw;
            .circle-sketch-highlight{
                position:relative;
              }
              
              .circle-sketch-highlight:before{
                content:"";
                z-index:-1;
                left:0em;
                top:0em;
                border-width:2px;
                border-style:solid;
                border-color:white;
                border-left-color:transparent;
                border-top-color:transparent;
                position:absolute;
                width:100%;
                height:1em;
                transform:rotate(-1deg);
                opacity:0.5;
                border-radius:0.25em;
                
              }
              
              .circle-sketch-highlight:after{
                content:"";
                z-index:-1;
                left:0em;
                top:0em;
                border-width:2px;
                border-style:solid;
                border-color:white;
                position:absolute;
                border-right-color:transparent;
                width:100%;
                height:1em;
                transform:rotate(2deg);
                opacity:0.5;
                border-radius:0.25em;
              }
        }
        p{  
            color: #ccff00;
            text-shadow: 0px 0px  black;
            padding-top: -5vw;
            text-align: left;
            width: 50vw;
            padding-left: 5vw;
            padding-right: 5vw;
            text-transform: none;
            font-size: 1.5vw;
            font-weight: 500;
            line-height: 1.12;
            font-family: "Fraktion Mono"
        }
    }
    
    .form{
        font-family: "Supply";
        left: 20vw;
        position: relative;
        #form-content{
            width: 60vw;
            form{
                .form-group{
                    
                    font-size: 2vw;
                    margin-top: 2vw;
                    input{
                        width: 100%;
                        font-size: 2vw;
                        border: solid black 0.1vw;
                        border-radius: 50px;
                        font-family: "Fraktion Mono";
                        font-weight: 400;
                    }
                    textarea{
                        margin-top: 2vw;
                        width: 100%;
                        font-size: 2vw;
                        border: solid black 0.1vw;
                        border-radius: 50px;
                        resize: vertical;
                        font-family: "Fraktion Mono";
                        font-weight: 400;
                    }

                }

                button{
                    width: 100%;
                    font-size: 2vw;
                    background-color: black;
                    color: white;
                    font-weight: 700;
                    font-family: "Supply";
                    border-radius: 50px;
                    cursor: pointer;
                    &:hover {
                        text-decoration: line-through;
                    }
                }
            }

        }
        display: flex;
        flex-direction: row;
        background-color: white;
        color: black;
        padding-left: 5vw;
        padding-right: 5vw;
        border-left: solid black 0.2vw;
       
        #slide-btn{ 
            font-size: 2vw;
            margin-top: 1vw;
            margin-right:54vw;
            padding: 0.5vw;
            text-align: center;
            border: black 0.2vw solid;
            border-radius: 50px;
            cursor: pointer;
            &:hover {
                text-decoration: line-through;
            }

        }
        #close-btn{ 
            font-size: 1.8vw;
            margin-left:54vw;
            margin-top: 1vw ;
            padding: 0.5vw;
            text-align: center;
            border: black 0.2vw solid;
            border-radius: 50px;
            cursor: pointer;
            &:hover {
                text-decoration: line-through;
            }
        }
    }

}