@font-face {
    font-family: "Fraktion Mono";
    src: url("./fonts/Fraktion/Mono/PPFraktionMono-Regular.otf") format("opentype");
}

@font-face {
    font-family: "Supply";
    src: url("./fonts/Supply/PPSupplySans-Regular.otf") format("opentype");
    font-weight: bold;
}
body {
    margin: 0;
    font-size: 0;
    overflow: hidden;
}

.loader-wrapper {
    height: 100vh;
    display: flex;
    background: black;
    flex-direction:column;
    justify-content: center;
    align-items: center;
    color: white;
    font-family: 'Supply';
    font-weight: 700;
    text-align: center;
    font-size: 12vw;

}

